import React, {Component} from 'react';

export default class Logo extends Component {
  render() {
    return <div>
            <div className="row">
              <div className="small-6 text-center small-centered columns">
              <img className="logo" src="/img/logo.png" />
              </div>
            </div>
        </div>
  }
}



